<template>
    <div class="page-container">
        <el-aside class="page-nav-left">
            <div class="aside-bar">
                <el-menu default-active="2">
                    <router-link to="/farming/task">
                        <el-menu-item index="0">
                            <i class="el-icon-date"></i>
                            <span slot="title">任务管理</span>
                        </el-menu-item>
                    </router-link>
                    <router-link to="/farming/seed">
                        <el-menu-item index="1">
                            <i class="el-icon-coin"></i>
                            <span slot="title">种子管理</span>
                        </el-menu-item>
                    </router-link>
                    <router-link to="/farming/tour">
                        <el-menu-item index="2">
                            <i class="el-icon-truck"></i>
                            <span slot="title">巡园管理</span>
                        </el-menu-item>
                    </router-link>
                    <router-link to="/farming/work">
                        <el-menu-item index="3">
                            <i class="el-icon-notebook-1"></i>
                            <span slot="title">工作汇报</span>
                        </el-menu-item>
                    </router-link>
                </el-menu>
            </div>
        </el-aside>
        <el-main class="m-l-220 m-t-20 bg-white">
            <div class="flex flex-align-center flex-pack-justify">
                <div class="flex flex-align-center">
                    <el-input class="m-r-15" size="small" style="width:200px;" placeholder="请输入巡园记录" suffix-icon="el-icon-search" v-model="vm.searchKey"></el-input>
                    <el-date-picker class="m-r-15" size="small" v-model="vm.searchDate" type="datetimerange" :picker-options="pickerOptions" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期" align="right"></el-date-picker>
                    <el-button type="primary" size="small" @click="search">搜索</el-button>
                </div>
                <el-button type="primary" size="small" @click="add">添加巡园记录</el-button>
            </div>
            <div class="tour-list m-t-30 clearfix">
                <div class="tour-item">
                    <el-image :src="url" :preview-src-list="srcList"></el-image>
                    <p>2019-09-28 12:20:56</p>
                </div>
                <div class="tour-item">
                    <el-image :src="url1" :preview-src-list="srcList1"></el-image>
                    <p>2019-09-28 12:20:56</p>
                </div>
                <div class="tour-item">
                    <el-image :src="url" :preview-src-list="srcList"></el-image>
                    <p>2019-09-28 12:20:56</p>
                </div>
                <div class="tour-item">
                    <el-image :src="url1" :preview-src-list="srcList1"></el-image>
                    <p>2019-09-28 12:20:56</p>
                </div>
                <div class="tour-item">
                    <el-image :src="url" :preview-src-list="srcList"></el-image>
                    <p>2019-09-28 12:20:56</p>
                </div>
                <div class="tour-item">
                    <el-image :src="url1" :preview-src-list="srcList1"></el-image>
                    <p>2019-09-28 12:20:56</p>
                </div>
                <div class="tour-item">
                    <el-image :src="url" :preview-src-list="srcList"></el-image>
                    <p>2019-09-28 12:20:56</p>
                </div>
                <div class="tour-item">
                    <el-image :src="url1" :preview-src-list="srcList1"></el-image>
                    <p>2019-09-28 12:20:56</p>
                </div>
            </div>
            <el-row class="m-t-30">
                <el-col>
                    <el-pagination 
                        class="text-right" 
                        background 
                        layout="total, prev, pager, next" 
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page.sync="vm.currentPage"
                        :page-size="vm.pageSize"
                        :total="vm.pageTotal">
                    </el-pagination>
                </el-col>
            </el-row>
            <el-dialog title="巡园记录" :visible.sync="dialogVisible" width="800px" :close-on-click-modal="false">
                <el-form ref="form" size="small" :rules="rules" :model="form" label-width="150px">
                    <el-form-item label="所属地块" prop="plot">
                        <el-input style="width:260px;" v-model="form.plot" disabled></el-input>
                    </el-form-item>
                    <el-form-item label="巡园时间" prop="tourTime">
                        <el-date-picker style="width:260px;" type="datetime" placeholder="选择日期" v-model="form.tourTime"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="巡园图片" prop="tourPics">
                        <el-upload
                            multiple
                            action="https://jsonplaceholder.typicode.com/posts/"
                            :limit="10"
                            :file-list="form.tourPics">
                            <el-button size="small" type="primary">点击上传</el-button>
                            <div slot="tip">只能上传jpg/png文件，且不超过500kb</div>
                        </el-upload>
                    </el-form-item>
                </el-form>
                <span slot="footer" class="dialog-footer">
                    <el-button size="small" @click="dialogVisible = false">取消</el-button>
                    <el-button size="small" type="primary" @click="save">保存</el-button>
                </span>
            </el-dialog>
        </el-main>
    </div>
</template>

<script>
export default {
    data() {
        return {
            vm: {
                searchKey: '',
                searchDate: '',
                currentPage: 1,
                pageSize: 10,
                pageTotal: 100,
            },
            pickerOptions: {
                shortcuts: [{
                    text: '最近一周',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                        picker.$emit('pick', [start, end]);
                    }
                }, {
                    text: '最近一个月',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                        picker.$emit('pick', [start, end]);
                    }
                }, {
                    text: '最近三个月',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                        picker.$emit('pick', [start, end]);
                    }
                }]
            },
            url: 'https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg',
            srcList: [
                'https://fuss10.elemecdn.com/8/27/f01c15bb73e1ef3793e64e6b7bbccjpeg.jpeg',
                'https://fuss10.elemecdn.com/1/8e/aeffeb4de74e2fde4bd74fc7b4486jpeg.jpeg'
            ],
            url1: 'https://fuss10.elemecdn.com/a/3f/3302e58f9a181d2509f3dc0fa68b0jpeg.jpeg',
            srcList1:[
                'https://fuss10.elemecdn.com/a/3f/3302e58f9a181d2509f3dc0fa68b0jpeg.jpeg',
                'https://fuss10.elemecdn.com/1/34/19aa98b1fcb2781c4fba33d850549jpeg.jpeg',
                'https://fuss10.elemecdn.com/0/6f/e35ff375812e6b0020b6b4e8f9583jpeg.jpeg',
                'https://fuss10.elemecdn.com/9/bb/e27858e973f5d7d3904835f46abbdjpeg.jpeg',
                'https://fuss10.elemecdn.com/d/e6/c4d93a3805b3ce3f323f7974e6f78jpeg.jpeg',
                'https://fuss10.elemecdn.com/3/28/bbf893f792f03a54408b3b7a7ebf0jpeg.jpeg',
                'https://fuss10.elemecdn.com/2/11/6535bcfb26e4c79b48ddde44f4b6fjpeg.jpeg'
            ],
            dialogVisible: false,
            form:{
                plot: '东里旺旺葡萄园',
                tourTime: '',
                tourPics: []
            },
            rules: {
                tourTime: [
                    { required: true, message: '请选择日期', trigger: 'change' }
                ],
                tourPics: [
                    { type: 'array', required: true, message: '请上传图片', trigger: 'change' }
                ]
            }
        }
    },
    computed:{
        tableHeight() {
            return document.documentElement.clientHeight - 275;
        }
    },
    methods:{
        search(){

        },
        add(){
            this.dialogVisible = true
        },
        edit(row) {
            this.resetForm()
            this.dialogVisible = true;
            this.form = row
        },
        save(){
            this.$refs['form'].validate((valid) => {
                if (valid) {
                    console.log(this.form)
                    this.dialogVisible = false
                } else {
                    this.$message.error("请检查您的输入！");
                    return false;
                }
            });
        },
        resetForm() {
            this.form = {
                plot: '东里旺旺葡萄园',
                tourTime: '',
                tourPics: []
            }
        },
        handleSizeChange(val) {
            console.log(val);
        },
        handleCurrentChange(val) {
            console.log(val);
        }
    }
}
</script>

<style>

</style>


